.chat{
    position: absolute;
    width: 20%;
    height: 45%;
    left: 1em;
    bottom: 8em;
    border: 1px solid rgba(211, 211, 211, 0.753);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.messagesContainer{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: rgba(236, 236, 236, 0.753);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    overflow-y: auto;
}

.messageInputContainer{
    width:100%;
    display: flex;
    height: 2em;
    border: 1px solid rgba(211, 211, 211, 0.753);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.messageInput{
    display: inline;
    border-radius: 5px;
    width: 90%;
    padding: 0.20em;
    height: 2em;
    border: none;
    border-bottom-left-radius: 10px;
}

.sendMessageBtn{
    display: inline-block;
    width: 10%;
    height: 100%;
    margin-left: 5px;
    border-bottom-right-radius: 10px;
    transition: color 0.25s ease-in-out, background-color 0.25s ease-in-out;
}

.sendMessageBtn:hover{
    background-color: black;
    color: white;
}

.sendIcon{
    margin-top: 2px;
}

.messageContainer{
    display: block;
    width: 50%;
    transform: translateX(100%);
    overflow-wrap: break-word;
}

.otherMessageContainer{
    display: block;
    width: 50%;
    overflow-wrap: break-word;
}

.sender{
    display: inline;
    font-style: italic;
    color: grey;
    padding: 0.5em;
}

.message{
    padding: 0.5em;
}

.myMessage{
    background-color: rgb(69, 112, 230);
    color: white;
    margin-right: 0.5em;
    border: 1px solid rgb(69, 112, 230);
    border-radius: 10px;
}

.otherClientMessage{
    background-color: lightgrey;
    color: black;
    margin-left: 0.5em;
    border: 1px solid lightgrey;
    border-radius: 10px;
}