.roomContainer{
    position: relative;
}

.videoFeedsContainer{
    position: fixed;
    right: 0;
    padding: 0 1.5em 0 1.5em;
    height: 100%;
}

.myVideoContainer, .otherClientVideoContainer{
    position: relative;    
}

.name, .otherClientName{
    background-color: rgba(97, 97, 97, 0.7);
    color: white;
    z-index: 1;
    position: absolute;
    top: 0;
    width: 100%;
}

.videoFeed {
    border: 1px solid black;
    margin-top: 1em;
    width: 300px;
    height: auto;
    max-width: 300px;
    max-height: 300px;
}

.myScreenContainer{

}


.stopVideoButton, .muteButton, .screenShareBtn{
    display: inline-block;
    padding: 0.5em;
    margin: 0.5em;
    border: 1px solid black;
    border-radius: 50%;
    width: 2em;
    height: 2em;
    transition: background-color 0.25s ease-in-out, color 0.25s ease-in-out;
}

.stopVideoButton:hover, .muteButton:hover, .screenShareBtn:hover{
    background-color: black;
    color: white;
}

.canvas {
    height: 100%;
    border: none;
}
.otherScreenContainer{
    height: 100%;
    width: 80%;
}

.otherClientScreenVid{
    height: 100%;
    width: 100%;
}

.tools{
    position: absolute;
    top: 2%;
    background-color: lightgrey;
    box-shadow: 5px 5px 5px darkslategrey;
}

.tool{
    border: 1px solid black;
    width: 1em;
    height: 1em;
    padding: 1em;
    transition: background-color 0.25s ease-in-out, color 0.25s ease-in-out;
}

.tool:hover{
    background-color: white;
    color: black;
}

.colorPicker{
    position: absolute;
    z-index: 1;
    left: 3em;
}

.timer{
    width: 20%;
    margin: 0 auto;
}

.chatBtn{
    position: absolute;
    bottom: 2rem;
    margin-left: 2em;
    font-size: 14pt;
    font-weight: bold;
    border: 1px solid black;
    padding: 0.5em 1em 0.5em 1em;
    border-radius: 10px;
    text-align: center;
    transition: color 0.25s ease-in-out, background-color 0.25s ease-in-out;
}

.chatBtn:hover{
    background-color: black;
    color: white;
}